// Media Queries
@mixin small {
  @media only screen and (min-width: #{$small}) {
    @content; } }

@mixin medium {
  @media only screen and (min-width: #{$medium}) {
    @content; } }

@mixin large {
  @media only screen and (min-width: #{$large}) {
    @content; } }

@mixin extra {
  @media only screen and (min-width: #{$extra}) {
    @content; } }

